import {
  Box,
  Typography,
  Button,
  Checkbox,
  ButtonGroup,
} from "@mui/material";
import BarChart from "./Charts";
import { BubbleChart, HorizontalChart } from "./Charts";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import KeyboardArrowDownOutlinedIcon from "@mui/icons-material/KeyboardArrowDownOutlined";
import EmailOutlinedIcon from "@mui/icons-material/EmailOutlined";

const Feed = () => {
  const boxStyle = {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "30px 5px",
  };

  const barChartContainerStyle = {
    width: 450,
  };

  const iconBarStyle = {
    position: "relative",
    left: "46%",
    display: "flex",
    gap: "15px",
  };

  return (
    <Box p={3}>
      <Box style={{ display: "flex", alignItems: "center" }}>
        <ChevronLeftIcon fontSize="medium" />
        <Typography variant="h4" sx={{ fontSize: "28px" }}>
          Default Playlist of BigFix Environment
        </Typography>
        <Box style={iconBarStyle}>
          <Button variant="contained" sx={{ height: "2%" }}>
            Export in PDF
          </Button>
          <Button variant="outlined" sx={{ height: "33px" }}>
            <EmailOutlinedIcon sx={{ marginRight: "5px" }} />
            Share
          </Button>
        </Box>
      </Box>

      <Box
        style={{
          backgroundColor: "white",
          marginTop: "20px",
          marginBottom: "20px",
          padding: "10px",
          borderRadius: "10px",
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
        }}
      >
        <Typography>Details</Typography>
        <Typography>
          Analyze main KPIs related to the Linux patching campaign
        </Typography>
        <KeyboardArrowDownOutlinedIcon />
      </Box>

      <Box
        style={{
          marginTop: "20px",
          marginBottom: "20px",
          padding: "10px",
          borderRadius: "10px",
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            alignItems: "center",
            gap: "30px",
          }}
        >
          <Typography>4 widgets</Typography>
          <div>
            <Checkbox />
          </div>
        </div>
        <div
          style={{
            marginRight: "10px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            gap: "10px",
          }}
        >
          <Typography>View</Typography>
          <div>
            <ButtonGroup variant="outlined" aria-label="outlined button group">
              <Button variant="contained" sx={{borderRadius: "20px 0px 0px 20px"}}>Slider</Button>
              <Button sx={{borderRadius: "0px 20px 20px 0px"}}>Grid</Button>
            </ButtonGroup>
          </div>
        </div>
      </Box>

      <Box style={boxStyle}>
        <div style={barChartContainerStyle}>
          <HorizontalChart />
        </div>
        <div style={barChartContainerStyle}>
          <BarChart />
        </div>
        <div style={barChartContainerStyle}>
          <BubbleChart />
        </div>
      </Box>
    </Box>
  );
};

export default Feed;
